#app {
  font-family: "Helvetica Neue", Helvetica, "PingFang SC", "Hiragino Sans GB",
    "Microsoft YaHei", "微软雅黑", Arial, sans-serif;
  color: #756c83;
  /* background-color: #092E49; */
  padding: 2px 2px 10px 2px;
}
/* 浮动布局 */
.pos-float {
  float: left;
}
/* 右侧内容背景色 */
div.pos-float {
  background: linear-gradient(to top right, #ecf7ff, #d8efff);
}
/* 整个页面背景色 */
body {
  background-color: #003d75;
}

/*定义滚动条高宽及背景 高宽分别对应横竖滚动条的尺寸*/
::-webkit-scrollbar {
  width: 9px;
  height: 9px;
}
/*定义滚动条轨道*/
::-webkit-scrollbar-track {
  background-color: transparent;
  -webkit-border-radius: 2em;
  -moz-border-radius: 2em;
  border-radius: 2em;
}
/*定义滑块 内阴影+圆角*/
::-webkit-scrollbar-thumb {
  background-color: rgba(170, 170, 170, 0);
  -webkit-border-radius: 2em;
  -moz-border-radius: 2em;
  border-radius: 2em;
}
.el-aside::-webkit-scrollbar-thumb,
.el-table--scrollable-x .el-table__body-wrapper::-webkit-scrollbar-thumb {
  background-color: rgb(170, 170, 170);
  -moz-border-radius: 2em;
  border-radius: 2em;
}
/* elementui的icon使用fontawesome图标 */
[class^="el-icon-fa"],
[class*=" el-icon-fa"] {
  display: inline-block;
  font: normal normal normal 14px/1 FontAwesome !important;
  font-size: inherit;
  text-rendering: auto;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.el-table--scrollable-x .el-table__body-wrapper:hover {
  overflow-x: auto;
}
.el-table--scrollable-x .el-table__body-wrapper:not(hover) {
  overflow-x: hidden;
}

.el-message__icon.el-icon-success {
  content: "\E79C";
  font-size: x-small;
}
.el-submenu__icon-arrow:hover {
  cursor: pointer;
}
i.el-submenu__icon-arrow {
  padding: 0px 10px;
  font-size: 16px;
  font-weight: bold;
}
/*遮罩层样式*/
.create-isLoading > .el-loading-spinner {
  top: 50%;
  left: 50%;
  margin-left: -55px;
  background: rgba(0, 0, 0, 0.7);
  padding: 20px;
  border-radius: 4px;
  width: auto;
  text-align: center;
  position: absolute;
}
.create-isLoading > .el-loading-spinner > .i {
  color: #eee;
}
.create-isLoading > .el-loading-spinner > .el-loading-text {
  color: #eee;
}
