.introjs {
  background-color: transparent !important;
  box-shadow: 0 0 8px 0 red !important;
}

.intro-base,
.intro-no-pre,
.intro-no-skip,
.intro-no-skip-and-end,
.intro-no-skip-and-end-and-next,
.intro-useradd {
  width: 300px;
  font-size: 17px;
  border-radius: 8px;
  background-color: #fff;
  border: 1px solid #ebeef5;
  padding: 14px 26px 14px 13px;
  box-shadow: 0 2px 12px 0 rgba(0, 0, 0, 0.4);
}

.intro-base::before,
.intro-no-pre::before,
.intro-no-skip::before,
.intro-no-skip-and-end::before,
.intro-no-skip-and-end-and-next::before,
.intro-useradd::before {
  content: "引导";
  font-size: 20px;
  font-weight: 600;
  margin-bottom: 20px;
  display: inline-block;
}

.intro-base .introjs-button,
.intro-no-pre .introjs-button,
.intro-no-skip .introjs-button,
.intro-no-skip-and-end .introjs-button,
.intro-no-skip-and-end-and-next .introjs-button,
.intro-useradd .introjs-button {
  font-size: 15px;
  color: #8ebcff;
  background: #fff;
  border: 1px solid #8ebcff;
}

.intro-base .introjs-tooltiptext,
.intro-no-pre .introjs-tooltiptext,
.intro-no-skip .introjs-tooltiptext,
.intro-no-skip-and-end .introjs-tooltiptext,
.intro-no-skip-and-end-and-next .introjs-tooltiptext,
.intro-useradd .introjs-tooltiptext {
  text-indent: 2em;
}

.intro-no-pre .introjs-prevbutton,
.intro-no-skip .introjs-prevbutton,
.intro-no-skip-and-end .introjs-prevbutton,
.intro-no-skip-and-end-and-next .introjs-prevbutton,
.intro-useradd .introjs-prevbutton {
  display: none;
}

.intro-no-skip .introjs-skipbutton,
.intro-no-skip-and-end .introjs-skipbutton,
.intro-no-skip-and-end-and-next .introjs-skipbutton,
.intro-useradd .introjs-skipbutton {
  display: none;
}
.intro-no-skip .introjs-donebutton,
.intro-no-skip-and-end .introjs-donebutton,
.intro-no-skip-and-end-and-next .introjs-donebutton,
.intro-useradd .introjs-donebutton {
  display: inline-block;
}

.intro-no-skip-and-end .introjs-skipbutton,
.intro-no-skip-and-end-and-next .introjs-skipbutton,
.intro-useradd .introjs-skipbutton {
  display: none;
}

.intro-no-skip-and-end-and-next .introjs-nextbutton,
.intro-useradd .introjs-nextbutton {
  display: none;
}

.intro-useradd {
  left: calc(50% + 300px) !important;
  top: calc(15vh + 141px) !important;
  margin: 0 !important;
}
